import { useParams } from "react-router";
// import useFetch from "../hooks/useFetch";
import { useHistory } from "react-router-dom"
import { useEffect, useState } from "react";
import { projectFireStore } from "../firestore/config";

const BlogDetails = () => {

  const history = useHistory()

  const { id } = useParams("id")

  const [ blog, setBlog ] = useState({})

  useEffect( () =>{
  //   const unsub = projectFireStore.collection("blogs")
  //     .onSnapshot( snap => {
  //       let documents = {}
  //       // let ourBlog = snap.filter( doc => doc.id == id)
  //       snap.forEach( doc => {
  //         setD(doc.data())
  //         documents.push({...doc.data(), id: doc.id })
  //         // setIsPending(false)
  //         // setError(null)
  //         // console.log(documents);
  //       });
  //       setBlog(documents)
  //       return () => unsub()
  //     })

    const dummyFun = ( async () =>{
      let docRef = projectFireStore.collection("blogs").doc(id);

      let dummyBlog = {}

      await docRef.get().then((doc) => {
        if (doc.exists) {
          console.log(doc.data());
          dummyBlog = doc.data()
          setBlog({...dummyBlog})
        } else {
          // doc.data() will be undefined in this case
          console.log("No such document!");
        }
      }).catch((error) => {
          console.log("Error getting document:", error);
      });
      console.log(blog)
    })
    dummyFun();
    // console.log(blog)
  }
  ,[id])

  // const { id } = useParams("id")

  // const { data:blog } = useFetch('http://localhost:8000/blogs/' + id)

  // const history = useHistory()

  const handleClick = () =>{
    // fetch('http://localhost:8000/blogs/' + blog.id,{
    //   method:'DELETE'
    // }).then( () =>
    //   history.push('/')
    // )
    projectFireStore.collection("blogs").doc(id).delete().then(() => {
      console.log("Document successfully deleted!");
      history.push('/');
    }).catch((error) => {
        console.error("Error removing document: ", error);
    });
  }

  return (
    <div className="blog-details">
      { blog && <article>
        { blog.title &&<h2>Blog Detail -  {blog.title} </h2> }
        { blog.author && <p>Written by {blog.author}</p>}
        { blog.body && <p>{blog.body}</p>}
        <button onClick={handleClick}>delete</button>
      </article> }
    </div>
  );
}

export default BlogDetails;