import { useState } from "react"
import { useHistory } from "react-router-dom"
import { projectFireStore,timestamp } from "../firestore/config"

const Newblog = () => {

  const [title, setTitle] = useState('')
  const [body, setBody] = useState('')
  const [author, setAuther] = useState('reda')
  const [isPending, setIsPending] = useState(false)
  const history = useHistory()
  const createdAt = timestamp();

  const blog = { title, body, author, createdAt };

  const HandleClick = (e) =>{
    e.preventDefault();

    setIsPending(true);

    const blogs = projectFireStore.collection('blogs');

    blogs.add(blog).then( () => {
      setIsPending(false);
      // alert("Your message has been submitted👍");
      history.push('/');
    })
    .catch((error) => {
      alert(error.message);
      setIsPending(false);
    });

    // const { addBlog } = useFirestore('blogs')
    // addBlog(blog)

    // fetch('http://localhost:8000/blogs', {
    //   method: 'POST',
    //   headers: { 'Content-Type' : 'application/json'},
    //   body: JSON.stringify(blog)
    // }).then( () =>{
    //   setIsPending(false)
    //   history.push('/')
    // })
  }

  return (
    <div className="new-blog">
      <h2>New Blog</h2>
      <form>
        <label>Blog title:</label>
        <input
          type="text" 
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
        <label>Blog body:</label>
        <textarea
          value={body}
          onChange={(e) => setBody(e.target.value)}
        ></textarea>
        <label>Blog auther:</label>
        {/* <select
          value={author}
          onChange={(e) => setAuther(e.target.value)}
        >
          <option value="hazem">hazem</option>
          <option value="reda">reda</option>
        </select> */}
        <input
          type="text" 
          value={author}
          onChange={(e) => setAuther(e.target.value)}
        />
        {!isPending && <button onClick={HandleClick}>Add Blog</button>}
        {isPending && <button onClick={HandleClick}>Adding Blog...</button>}
        {/* <p>{title}</p>
        <p>{author}</p>
        <p>{body}</p> */}
      </form>
    </div>
  );
}

export default Newblog;