import { Link } from "react-router-dom";

const Navbar = () => {
  // const handleChange = (e) => {
  //   console.log(e.target.value);
  // }

  const newBlogStyle = {
    backgroundColor: '#f2345f',
    borderRadius: 8,
    color : '#fff',
    padding : 5
  }

  return (
    <nav className='navbar'>
      <h1>The Gang Blog</h1>
      <div className='links'>
        <Link to='/'>Home</Link>
        <Link to='/create' style={newBlogStyle}>New Blog</Link>
      </div>
    </nav>
  );
}

export default Navbar;